:root {
  --primary-blue: hsl(219, 29%, 15%);
  --secondary-blue: hsl(227, 89%, 85%);
  --accent-orange: hsl(31, 100%, 67%);
  --accent-green: hsl(87, 46%, 63%);
  --accent-purple: hsl(260, 54%, 67%);
  --text-color: hsl(217, 100%, 97%);
  --inactive-text-color: hsla(216, 86%, 67%, .40);
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--primary-blue);
  color: var(--text-color);
}

h1,
h2,
h3 {
  margin: 1.25rem;
  margin-bottom: 0;
}

h1 {
  font-size: 2.25rem;
  font-weight: 700;
}
@media (min-width: 64em) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.25rem;
  font-weight: 500;
  margin-top: 0.3125rem;
}

p {
  margin: 1.25rem;
  font-size: 0.875rem;
}

a,
a:visited,
a:active {
  margin-left: 1.25rem;
  text-decoration: none;
}

main {
  display: flex;
  flex-flow: row nowrap;
  padding: 0 6rem;
  max-width: 69.375rem;
}
@media (min-width: 64em) {
  main {
    padding: 0 6rem;
  }
}

.section__pane {
  padding-top: 25%;
}
.section__pane--left {
  min-width: 21.875rem;
  position: sticky;
  top: 20px;
}
.section__pane--right {
  min-width: 50.3125rem;
}

.title__location {
  display: flex;
  align-items: center;
  margin: 15px;
}
.title__location img {
  padding-right: 5px;
}

.nav {
  height: 25rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.nav__links {
  display: flex;
  list-style-type: none;
  padding: 0;
}
.nav__links--section {
  flex-direction: column;
  justify-content: space-around;
  height: 15vh;
}
.nav__links--social {
  flex-direction: row;
}

.link:hover {
  color: var(--text-color);
  filter: var(--accent-green);
}
.link--active {
  color: var(--text-color);
}
.link--inactive {
  color: var(--inactive-text-color);
}
.link__social {
  height: 24px;
}
.link__social--github, .link__social--linkedin {
  filter: invert(100%) sepia(3%) saturate(12%) hue-rotate(103deg) brightness(105%) contrast(105%);
}
.link__social--github:hover, .link__social--linkedin:hover {
  filter: invert(84%) sepia(28%) saturate(585%) hue-rotate(38deg) brightness(90%) contrast(85%);
}